.single{
    width: 95%;
    display: flex;
    padding: 3px;
    margin: 9px;
    background-color: aquamarine;
    justify-content: space-around;
    border-radius: 20px;
    box-shadow: 10px 10px 15px grey;
    
}

.todoname{
    width: 70%;
    margin: 5px;
    padding: 5px;
    overflow: hidden;
    /* text-overflow: clip;
    white-space: initial; */
    word-wrap: break-word;
    color: red;
}



.icon{
    
    cursor: pointer;
}

.icon:hover{
    transform: scale(0.9);
}